@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');
.App {
  text-align: center;
  padding: 2rem;
  font-family: 'Cinzel', serif;
}
/*DAY 3: we want to handle the theming of our website so here we are in the
lines of the styling sheet.
By the end of today I'm trying to get a dark purply background, some funky wizard font,
and a decent UI feature I've been thinking about since day 1*/

body {
  background: linear-gradient(to bottom, #1c0f2e, #100820);
  color: #f2f2f2;
  font-family: 'Cinzel', serif;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: transparent;
}

/*write a class with styling for our background stars*/
/*container for holding our stars*/
.star-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
}
/*Animation for making stars twinkle*/
@keyframes twinkle {
  0%, 100% {opacity: 0.9; transform: scale(1);}
  50% {opacity: 0.4; transform: scale(1.2);}
}
.star {
  position: absolute;
  width: 8px;
  height: 8px;
  background: radial-gradient(circle, gold 40%, transparent 40%);
  transform: rotate(45deg);
  box-shadow: 0 0 6px gold, 0 0 12px gold;
  border-radius: 50%;
  animation: twinkle 3s infinite ease-in-out;
}

/*we'll use psuedoelements to make the stars appear multi pointed 
We'll declare a glowing circle and rotate it a bit to get the desired effect*/
.star::before, .star::after {
  content: '';
  position: absolute;
  top: -50%;
  left: 0;
  width: 100%;
  height: 200%;
  /*we tried with ellipses to give the star more of a far away 'streaky' look, but that just made the stars look unshaped and ugly*/
  background: radial-gradient(circle, gold 40%, transparent 40%);
  transform: rotate(60deg);
  border-radius: 50%;
}
.star::after {
  transform: rotate(120deg);
}

/*bonus for day 3, headers which glow*/
.glow {
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
  text-align: center;
  text-shadow:
  0 0 5px #fff,
  0 0 10px #0ff,
  0 0 20px #0ff,
  0 0 30px #0ff,
  0 0 40px #0ff;
  animation: pulseGlow 2s infinite ease-in-out;
}
@keyframes pulseGlow {
  0%, 100% {
    text-shadow:
      0 0 5px #fff,
      0 0 10px #0ff,
      0 0 20px #0ff,
      0 0 30px #0ff,
      0 0 40px #0ff;
  }
  50% {
    text-shadow:
      0 0 10px #fff,
      0 0 20px #0ff,
      0 0 30px #0ff,
      0 0 40px #0ff,
      0 0 50px #0ff;
  }
}

input {
  padding: 0.5rem;
  width: 250px;
  margin-right: 0.5rem;
  font-family: inherit;
  background: #3b1e3f;
  color: #bfa757;
  opacity: 1;
}

input::placeholder {
  color: #bfa757;
  opacity: 1;
}

button {
  background-color: #2d1a40;
  color: #f2e6ff;
  border: 1px solid gold;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-family: inherit;
  font-weight: bold;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

button:hover {
  background-color: #4b2c6d;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.card {
  border: 1px solid gold;
  padding: 0.5rem;
  background: #3b1e3f;
  color: #bfa757;
  display: inline-block;
  text-align: center;
  max-width: 100%;
  white-space: normal;
}

.card-images {
  display: inline-flex;
  flex-direction: row;
  gap: 8px;
  justify-content: center;
  flex-wrap: nowrap;
}

.card-images img {
  max-width: 150px;
  height: auto;
  display: block;
}

/*Add a card preview for hovering over like in mtg arena*/
.card-preview {
  width: 300px;
  position: fixed;
  z-index: 5;
  pointer-events: none;
  transform: translate(15px, 15px);
}

.card-preview img {
  width: 300px;
  height: auto;
  border-radius: 8px;
  border: 2px solid gold;
  box-shadow: 0 0 15px rgba(0,0,0,0.0);
  pointer-events: none;
}


/*don't forget split cards!*/
.split-card {
  transform: rotate(90deg);
  transform-origin: center;
  width: auto;
  height: 150px;
  margin-right: 8px;
}

textarea {
  font-family: inherit;
  background: #3b1e3f;
  color: #bfa757;
  opacity: 1;
}
textarea::placeholder { 
  color: #bfa757;
  opacity: 1;
}

.deck-select {
 /*Remove default styling*/
 appearance: none;
 -webkit-appearance: none;
 -moz-appearance: none;
 background-color: #2d1a40;
 color: #f2e6ff;
 border: 1px solid gold;
 padding: 0.5rem 1rem;
 border-radius: 5px;
 font-family: inherit;
 font-weight: bold;
 transition: background-color 0.3s ease;
 cursor: pointer;
 
 /*dropdown arrow*/
 padding-right: 2rem;
 /*super dark evil svg tech
 but seriously, nothing scary
 data:image/svg+xml;utf8 just tells the browser we're going to read an svg image encoded as utf8
 then xmlns confirms that this is a svg,
 finally the path tag, specifically d='...' are just directions on how to draw the image
 M7 10 (move pen to x=7,y=10)
 l5 5 draw a line (relative to current position) 5 right, 5 down
 l5 -5 5 right, 5 up
 z close the shape (making the v for our dropdown arrows)
 */
 background-image: url("data:image/svg+xml;utf8,<svg fill='gold' height='12' viewBox='0 0 24 24' width='12' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/></svg>");
 background-repeat: no-repeat;
 background-position: right 0.5rem center;
 background-size: 1rem;
}

.deck-select:hover {
  background-color: #4b2c6d;
}